import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import TeamConnectBoxes from './connect-boxes';
import TeamConnectConfirm from './connect-confirm';

import Ad from '../ad/';

import FastList from './fastList';

import Overlay from '../overlay/';
import TeamsPublicDetail from '../../routes/teams/publicDetail';
import DogPublicDetail from '../../routes/dogs/publicDetail';

import TeamPublicFeedLine from './publicFeedLine';

import mu from '../../lib/musher-util';

const initialState = {
	showAddComment: {},
    // viewImageIdx: {},
    likeWorkout: {},
    idxSuggestion: 0,
    idxAd: 4,
    limit: 10,
    offset: 0,
    hasFollowSugguestions: false, 
    hasAd: false,
    sessionid: new Date().getTime(),
};

const dataFields = ['id', 'type', 'user', 'team', 'title'];

@observer
class TeamPublicFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    async loadTeamsPublicFeed({ props = this.props, limit, offset }) {
		const { teamStore } = props.stores;
        const { teamid } = props;
		await teamStore.loadPublicFeed({ limit, offset, sort: '-createdDate', teamid });
	}

    setLoadMore = () => {
        const { teamStore } = this.props.stores;
        teamStore.setLoadMore(() => {
            const { teamid } = this.props;
            let { limit, offset } = this.state;
            offset += limit;
            this.setState({ offset });
            this.loadTeamsPublicFeed({ limit, offset, teamid });
        });
    }

    unsetLoadMore = () => {
        const { teamStore } = this.props.stores;
        teamStore.setLoadMore(() => {});
    }

    async loadAll(props = this.props) {
        const { limit } = this.state;
        const { teamid } = props;
		await this.loadTeamsPublicFeed({ props, limit, offset: 0, teamid });
        // this.insertTopRanked();
        // this.insertAds();
        // this.insertTeamConfirm();
        // this.insertTeamSuggestions();
    }

    insertTopRanked = () => {
        const { teamStore } = this.props.stores;
        const { publicTeamsFeed, topRankedWorkouts } = teamStore;
        const topRankedRandomized = util.shuffleArray(topRankedWorkouts);
        const feed = [...publicTeamsFeed];
        for (let i = 0, l = 3; i < l; i += 1) {
            const randomPlace = mu.randomInt(0, 7);
            if (util.isObject(topRankedRandomized[i])) {
                feed.splice(randomPlace, 0, topRankedRandomized[i]);
            }
        }
        teamStore.localUpdateField('publicTeamsFeed', feed);
    }

    insertAds = () => {
        const { idxAd } = this.state;
        const { teamStore } = this.props.stores;
        const { publicTeamsFeed } = teamStore;
        const feed = [...publicTeamsFeed];
        feed.splice(idxAd, 0, { type: 'ad', wrapperClassNames: '', contentClassNames: '' });
        teamStore.localUpdateField('publicTeamsFeed', feed);
    }

    insertTeamConfirm = () => {
        const { idxConfirm } = this.state;
        const { teamStore } = this.props.stores;
        const { publicTeamsFeed } = teamStore;
        const feed = [...publicTeamsFeed];
        feed.splice(idxConfirm, 0, { type: 'teamConfirm', wrapperClassNames: '', contentClassNames: '' });
        teamStore.localUpdateField('publicTeamsFeed', feed);
    }

    insertTeamSuggestions = () => {
        const { idxSuggestion } = this.state;
        const { teamStore } = this.props.stores;
        const { publicTeamsFeed } = teamStore;
        const feed = [...publicTeamsFeed];
        feed.splice(idxSuggestion, 0, { type: 'teamSuggestions', wrapperClassNames: '', contentClassNames: '' });
        teamStore.localUpdateField('publicTeamsFeed', feed);
    }

    // Overlay functions
    onTeamClick = (e, teamid) => {
        e.preventDefault();
        e.stopPropagation();
        this.setState({
            showTeam: teamid,
        });
        return false;
    }

    hideTeam = () => {
        this.setState({
            showTeam: null,
        });
    }

    onUserClick = (e, userid) => {
        e.preventDefault();
        e.stopPropagation();
        return false;
    }

    onDogClick = (e, dogid, dogs) => {
        e.preventDefault();
        e.stopPropagation();
        this.setState({
            showDog: dogid,
            dogs,
        });
        return false;
    }

    hideDog = () => {
        this.setState({
            showDog: null,
        });
    }

    // /Overlay functions

    // Scroll views timer
    setupIntersection = () => {
        this.setState({
            handleIntersection: mu.intersectionObserverCallback,
        });
        const { teamStore } = this.props.stores;
        this.scrollViewTimer = setInterval(() => teamStore.postScrollview(mu.getIntersectionObserverHistory()), 30 * 1000);
    }

    componentWillMount() {
        this.setupIntersection();
    }

    componentWillUnmount() {
        this.unsetLoadMore();
        const { teamStore } = this.props.stores;
        teamStore.postScrollview(mu.getIntersectionObserverHistory());
        clearInterval(this.scrollViewTimer);
        teamStore.cleanupMemory();
    }

	componentDidMount() {
        this.loadAll();
        this.setLoadMore();
	}

	componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps.viewmenu, this.props.viewmenu);
        if (nextProps.teamid !== this.props.teamid) {
            this.loadAll(nextProps);
        }
    }

    renderContent = ({ obj, idx }) => {
        const { appState } = this.props.stores;
        const { path } = appState;

        if (obj.type === 'ad') {
            return(<>
                <Ad stores={this.props.stores} path={path} />
            </>);
        }
        if (obj.type === 'teamConfirm') {
            return(<>
                <TeamConnectConfirm stores={this.props.stores} />
            </>);
        }
        if (obj.type === 'teamSuggestions') {
            return(<>
                <TeamConnectBoxes stores={this.props.stores} />
            </>);
        }
        return (<>
            <TeamPublicFeedLine
                stores={this.props.stores}
                feed={obj}
                idx={idx}
                onTeamClick={this.onTeamClick}
                onUserClick={this.onUserClick}
                onDogClick={this.onDogClick}
            />
        </>);
    }

    render() {
        const { showTeam, showDog, handleIntersection, sessionid, dogs } = this.state;
        const { teamStore, appState } = this.props.stores;
        const { publicTeamsFeed } = teamStore;
        const { path, darkmode } = appState;
        const { teamid } = this.props;
        const team = teamStore.findPublicTeamByUuidv4(teamid);
        
        const image = mu.getImage({ team });
        let shortname;
        let name;
        let username;
        if (team && team.name) {
            shortname = mu.displayNameShort(team);
            name = mu.displayName(team);
        }


        return (<>
            {showTeam && <Overlay>
                <TeamsPublicDetail stores={this.props.stores} teamid={showTeam} marginTop={'0px;'} marginBottom={'0px;'} back={this.hideTeam} hideTeam={this.hideTeam} />
            </Overlay>}

            {showDog && <Overlay>
                <DogPublicDetail stores={this.props.stores} dogid={showDog} marginTop={'0px;'} marginBottom={'0px;'} back={this.hideDog} dogs={dogs} />
            </Overlay>}

            <div class='w-100'>
                {team ? <>
                    <div class='row pb-3 pt-3'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
                            <div class='row'>
                                <h3>
                                    <div class='d-flex position-relative'>
                                        <div
                                            class={`text-center border rounded-circle imageRounded imageRoundedSmall`}
                                            style={image ? `background-image: url("${image}"); background-size: cover;` : ''}
                                        >
                                            {!image && <div class='text-muted' style='font-size: 20px; padding-top: 3px;'>{shortname}</div>}
                                        </div>
                                        <div class='flex-grow-1 pl-3 text-overflow' style='line-height: 1.2em;'>
                                            <h3 class='mb-1'>
                                                {team.public ? <>
                                                    <a href={`/teams/public/${team.uuidv4}`} class='stretched-link'>{name}</a>
                                                </> : <>
                                                    {name}
                                                </>}
                                            </h3>
                                        </div>
                                    </div>
                                </h3>
                            </div>
                        </div>
                    </div>
                </> : <>
                    <TeamConnectConfirm stores={this.props.stores} />
                </>}

                {(!publicTeamsFeed || publicTeamsFeed.length === 0) && <>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
                        <div class='text-center text-muted'>
                            <div class='display-1'><i class='fas fa-chart-area' /></div>
                            <h3><Text id='search.no-workouts-found'>No workouts found.</Text></h3>
                            <Text id='teams.no-public-workouts-text'>Please follow more teams to see their workout activity on this page. This is a great way to get updates from all your favorite mushers.</Text>
                        </div>
                        <div class='text-center mt-3'>
                            <a href='/teams/connect' class='btn btn-primary'>
                                <i class='fas fa-users' /> <Text id='header.connect-team'>Follow teams</Text>
                            </a>
                        </div>
                    </div>
                </>}

                <FastList
                    data={publicTeamsFeed}
                    wrapperClassNames={'row pb-4'}
                    contentClassNames={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 my-1 position-relative rounded rounded-lg py-3 bg-${darkmode ? 'black' : 'white'} shadow-sm`}
                    renderContent={this.renderContent}
                    dataFields={dataFields}
                    dataValues={{ type: '10' }}
                    handleIntersection={handleIntersection}
                    sessionid={sessionid}
                />

            </div>
        </>);
    }
}

export default TeamPublicFeed;
