import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

const MARKDOWN_OPTIONS = {
	pedantic: false,
	gfm: true,
	breaks: true,
	sanitize: false,
	smartLists: true,
	smartypants: true,
	xhtml: true,
};

import mu from '../../lib/musher-util';

function replaceImages(line = '', images, language = 'en') {
    function imageReplacer(match, p1, p2, p3, offset, string) {
        // console.log({ match, p1, p2, p3, offset, string })
        if (util.isObject(images[p1])) {
            return `<img src=${images[p1].s3XXLargeLink} class='img-fluid float-right' style='width: ${p2 || 40}%;' />`;
        }
        return '';
    }

    if (util.isString(line)) {
		let text = line;
        const regex = new RegExp(`<${language}>(.*?)</${language}>`, 's');
		const match = regex.exec(text);
		if (match) {
			const parsedLanguageText = match[0];
			if (parsedLanguageText) {
				text = parsedLanguageText;
			}
		}
        return text.replace(/\{\{img\.(\d+)\s*(\d*)\}\}/g, imageReplacer);
    }
    return line;
}

const initialState = {
	isLoading: false,
};

@observer
class Races extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    async loadRace(props = this.props) {
		this.setState({ isLoading: true });
		const { raceStore } = props.stores;
		const query = {};
		await raceStore.load({ query });
		this.setState({ isLoading: false });
	}

    toggleRaces = async () => {
		const { userStore } = this.props.stores;
        const { user } = userStore;
        const { toggleFeature = {} } = user;
        const value = toggleFeature.racesFrontpage ? 0 : 1;
        this.setState({ hideRaces: value });
        await userStore.setToggleFeature({
            field: 'toggleFeature.racesFrontpage',
            value,
        });
    }

    componentDidMount() {
		this.loadRace();
	}

	componentWillReceiveProps(nextProps) {
		this.loadRace(nextProps);
	}

    render() {
        const { appState, userStore, raceStore, newsStore } = this.props.stores;
		const { allUsers, isAdmin, user, notifications = [] } = userStore;
		const { races } = raceStore;
		const { newsList } = newsStore;
		const news = newsList && newsList[0] ? newsList[0] : null;
		const { darkmode, language = 'en' } = appState;
		const { showNews, hideRaces } = this.state;
        const { toggleFeature = {} } = user;

        return (
            <div class='row'>
                {hideRaces || toggleFeature.racesFrontpage ? <>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-1'>
                        <div class='row'>
                            <div class={`col-12 bg-${darkmode ? 'black' : 'white'} rounded-lg position-relative`}>
                                <div class='w-100 d-flex justify-content-center'>
                                    <button class='btn btn-block btn-link' onClick={this.toggleRaces}>
                                        <i class='fas fa-angle-down' /> <Text id='races.show-races'>Show Races</Text>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </> : <>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-3'>
                        <div class='row'>
                            {races && races.sort((a, b) => a.startDate - b.startDate).reverse().map(race =>  {
                                const today = new Date();
                                let status = '';
                                if (today >= new Date(race.startDate) && today <= new Date(race.endDate)) {
                                    status = 'race';
                                } else if (today >= new Date(race.signupStartDate) && today <= new Date(race.signupEndDate)) {
                                    status = 'signup';
                                } else if (today > new Date(race.endDate)) {
                                    return (<></>);
                                }

                                return (<>
                                    <div class={`col-12 mb-0 bg-${darkmode ? 'black' : 'white'} rounded-lg position-relative`}>
                                        <div class='row d-flex align-items-center position-relative'>
                                            <h5 class='position-absolute' style='top: 3px; right: 5px;'>
                                                {status === 'signup' && <>
                                                    <span class='badge badge-primary border border-white ml-2'><Text id='races.signup-open'>Påmelding åpen</Text></span>
                                                </>}
                                                {status === 'race' && <>
                                                    <span class='badge badge-danger border border-white ml-2'><Text id='races.race-in-progress'>Løp pågår</Text></span>
                                                </>}
                                                {status === 'done' && <>
                                                    <span class='badge badge-success border border-white ml-2'><Text id='races.race-finished'>Løp avsluttet</Text></span>
                                                </>}
                                            </h5>
                                            {(race.images && race.images[0]) ? <img src={race.images[0].s3LargeLink} class='img-fluid' /> : ''}
                                        </div>
                                        <div class='position-relative'>
                                            <h5 class='position-absolute' style='top: 10px; right: 10px;'>
                                                {status === 'signup' && <>
                                                    <div class='spinner-grow text-primary' role='status' />
                                                </>}
                                                {status === 'race' && <>
                                                    <div class='spinner-grow text-danger' role='status' />
                                                </>}
                                                {status === 'done' && <>
                                                    <span><i class='fas fa-check text-success' /></span>
                                                </>}
                                            </h5>
                                            <h5 class='pt-2'>
                                                {isAdmin && <i class={`fas fa-eye${race.active ? ' text-success' : '-slash text-danger'} float-right`} />}
                                                {race.title}
                                            </h5>
                                        </div>
                                        <div class='w-100'>
                                            <Markdown markdown={replaceImages(race.ingress, race.images, language)} markdownOpts={MARKDOWN_OPTIONS} />
                                        </div>
                                        <div class='w-100 d-flex justify-content-left mt-2'>
                                            {race.classes && <>
                                                <div class='mb-3'>
                                                    <small class='font-weight-lighter mr-2'><Text id='races.classes'>Klasser</Text>: </small><br />
                                                    {race.classes.sort((a, b) => a.distance - b.distance).map(currentClass => <>
                                                        <span class='badge badge-pill badge-light mr-2 font-weight-light'>
                                                            {currentClass.title}
                                                            {/* <span class='badge badge-info ml-2 font-weight-light'>{currentClass.contestants.length}</span> */}
                                                        </span>
                                                    </>)}
                                                </div>
                                            </>}
                                        </div>
                                        <div class='w-100 d-flex justify-content-end'>
                                            <a href={`/app-races/${race.id}`} class='btn btn-primary stretched-link'>
                                                <Text id='races.read-more'>Les mer</Text>
                                                <i class='fas fa-angle-double-right ml-2' />
                                            </a>
                                        </div>
                                        <div class='w-100 d-flex justify-content-center'>
                                            <small class='text-muted'>
                                                <Text id='races.race-days'>Løpsdager</Text>: {race.startDate && <>{util.formatDate(race.startDate, { locale: language })}</>}
                                                {race.endDate && <> - {util.formatDate(race.endDate, { locale: language })}</>}
                                            </small>
                                        </div>
                                    </div>
                                </>);
                            })}
                            <div class={`col-12 mb-3 bg-${darkmode ? 'black' : 'white'} rounded-lg position-relative`}>
                                <div class='w-100 d-flex justify-content-center'>
                                    <button class='btn btn-link' onClick={this.toggleRaces}>
                                        <i class='fas fa-angle-up' /> <Text id='races.hide'>Hide</Text>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </>}
            </div>
        );
    }
}

export default Races;
