import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { route } from 'preact-router';

import PubSub, { topics } from '../../lib/pubsub';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import InviteFriend from '../../components/user/invite';
import UserUpdate from '../../components/user/updateSettings';
import Install from '../../components/install';
import WorkoutTotals from '../../components/workout/totals';
import WorkoutStatus from '../../components/workout/status';
import Teams from '../../components/team/teams';
import TeamPublicFeed from '../../components/team/publicFeed';
import StoryAdd from '../../components/story/add';
import StoryPublicFeed from '../../components/story/publicFeed';
import YrWeather from '../../components/yrweather/';
import Races from '../../components/races/';

const initialState = {
	isLoading: false,
	submenu: 'lastWorkouts',
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		const { appState, userStore, messageStore } = this.props.stores;
		this.setState({ isLoading: true });
        await appState.getFingerprint();
        await userStore.getInfo();
		await userStore.getNotifications();
		await messageStore.getUnReadMessages();

		this.setState({ isLoading: false });
    }

	chooseSubmenu = (e) => {
		const submenu = e.target.parentElement.dataset.menu || e.target.dataset.menu;
		this.setState({ submenu });
	}

	componentDidMount() {
		// this.addNavigation();
		this.loadAll();
	}

	// Swipe navigation
	addNavigation = (props = this.props) => {
        const { viewmenu } = props;
        const { appState } = this.props.stores;
		if (viewmenu === 'wall') {
			appState.setSwipeRight();
			appState.setSwipeLeft(() => {
				appState.setSwipeLeft();
				route(`/home-workout`);
			});
		}
		if (viewmenu === 'workout') {
			appState.setSwipeLeft();
			appState.setSwipeRight(() => {
				appState.setSwipeRight();
				route(`/`);
			});
		}
	}

	getState = async () => {
		const { workoutStore } = this.props.stores;
		await workoutStore.cordovaGetState();
		await workoutStore.cordovaProviderState();
		await workoutStore.cordovaIsPowerSaveMode();
		await workoutStore.cordovaGetLog();
	}

	destroyLog = async () => {
		const { workoutStore } = this.props.stores;
		workoutStore.cordovaDestroyLog();
	}

	componentWillUnmount() {
		const { appState } = this.props.stores;
		appState.setSwipeRight();
		appState.setSwipeLeft();
	}
	// /Swipe navigation

	componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps.viewmenu, this.props.viewmenu);
        if (nextProps.viewmenu !== this.props.viewmenu) {
            // this.addNavigation(nextProps);
        }
    }

	render() {
		const { isLoading, submenu } = this.state;
		const { viewmenu, path, teamid } = this.props;
		const { userStore, appState, workoutStore } = this.props.stores;
		// const { workoutPlan, workoutPlans } = workoutPlanStore;
		const { darkmode, viewmode, apiServer } = appState;
		const { isAdmin, user } = userStore;
		const { currentLocationGeocode = {}, yrWeatherKeys = [] } = user;
		const { formatted_address: place } = currentLocationGeocode;

		const { currentTeam, cordovaActiveTrackingParams, cordovaAuthorizationStatus, powerSaveStatus,
			providerState, log, isRunning, totalDistance, geoLocationState, bgGeo, currentSpeed, totalTime, workouts } = workoutStore;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>

{/* <xmp>bgGeo: {JSON.stringify(bgGeo, null, 4)}</xmp> */}
{/* <button class='btn btn-success btn-block' onClick={this.getState}>Get State</button>
<button class='btn btn-success btn-block' onClick={this.destroyLog}>Destroy log</button>
<xmp>cordovaAuthorizationStatus: {JSON.stringify(cordovaAuthorizationStatus, null, 4)}</xmp>
<xmp>cordovaActiveTrackingParams: {JSON.stringify(cordovaActiveTrackingParams, null, 4)}</xmp>
<xmp>geoLocationState: {JSON.stringify(geoLocationState, null, 4)}</xmp>
<xmp>powerSaveStatus: {JSON.stringify(powerSaveStatus, null, 4)}</xmp>
<xmp>providerState: {JSON.stringify(providerState, null, 4)}</xmp>
<xmp>isRunning: {JSON.stringify(isRunning, null, 4)}</xmp>
<xmp>totalDistance: {JSON.stringify(totalDistance, null, 4)}</xmp>
<xmp>currentSpeed: {JSON.stringify(currentSpeed, null, 4)}</xmp>
<xmp>totalTime: {JSON.stringify(totalTime, null, 4)}</xmp>
<xmp>workoutStore.workouts: {JSON.stringify(workouts.length, null, 4)}</xmp>

{log && <>{JSON.stringify(log.split('\n').map(e => `${e}\n`), null, 4)}</>} */}


				{isRunning && cordovaActiveTrackingParams && <>
					<div class='row'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom bg-light py-4'>
							<h1 class='text-success font-weight-bold'>
								Tracking
								<div class='spinner-grow ml-3' role='status' />
							</h1>
							<div class='row mb-3'>
								<div class='col-4 text-center font-weight-bold' style='font-size: 1.3em;'>{util.secToHms(totalTime)}</div>
								<div class='col-4 text-center font-weight-bold' style='font-size: 1.3em;'>{util.format(currentSpeed * 3.6, 1)} km/t</div>
								<div class='col-4 text-center font-weight-bold' style='font-size: 1.3em;'>{util.format(totalDistance / 1000, 2)} km</div>
							</div>
							<a class='btn btn-block btn-danger' href='/workouts/tracking'><Text id='workouts.go-to-tracking'>Go to tracking</Text></a>
						</div>
					</div>
				</>}

				<div class=''>
					{user.id && <YrWeather stores={this.props.stores} place={place} yrWeather={yrWeatherKeys.join(',')} />}
				</div>

				{user.id && <StoryAdd stores={this.props.stores} />}

				<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
					<div class='row'>
						<div class='w-100 overflow-hidden'>
							<div
								class='d-flex flex-row flex-nowrap pb-2 px-1'
								style='overflow: auto; scroll-snap-type: x mandatory;'
							>					
								<div class='col px-0 pt-2'>
									<a
										class={`btn btn-block rounded-pill-left rounded-none ${viewmenu === 'wall' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-menu='wall'
										href='/home-wall'
									>
										<i class='fas fa-comments' /> <Text id='home.wall'>Veggen</Text>
									</a>
								</div>
								<div class='col px-0 pt-2'>
									<a
										class={`btn btn-block rounded-pill-right rounded-none ${viewmenu === 'workout' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-menu='workout'
										href='/home-workout'
									>
										<i class='fas fa-running' />  <Text id='home.workouts'>Treninger</Text>
									</a>
								</div>
								{/* <div class='col px-0 pt-2'>
									<a
										class={`btn btn-block ${viewmenu === 'totals' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
										style='text-overflow: ellipsis; overflow: hidden;'
										data-menu='totals'
										href='/home-totals'
									>
										<i class='fas fa-tachometer-alt' /> <Text id='home.totals'>Totaler</Text>
									</a>
								</div> */}
							</div>
						</div>
					</div>
				</div>


				{viewmenu === 'wall' && <>
					<Races stores={this.props.stores} />
					<StoryPublicFeed stores={this.props.stores} path={path} />
				</>}
				{viewmenu === 'workout' && <>
					<TeamPublicFeed stores={this.props.stores} teamid={teamid} />
				</>}
				{viewmenu === 'totals' && <>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
						<Teams stores={this.props.stores} />
						<WorkoutTotals stores={this.props.stores} currentTeam={currentTeam} />
						<WorkoutStatus stores={this.props.stores} currentTeam={currentTeam} />
					</div>
				</>}

			</div>
		);
	}
}

export default Home;
